.pictures img{
    aspect-ratio: 900/600;
    width: 50vw;
    height: 100%;
    display: flex;
    margin: 0;
    justify-content: center;
}

.center{
    display: grid;
    place-items: center;
}

.wrapper {
    width: fit-content;
    height: 100%;
    margin: 0;
}

.pictures .buttons {
    position: absolute;
    display: flex;
    flex-direction: row;
    border-style: solid;
    bottom: 0;
}

.pictures{
    position: relative;
    height: 100%;
}