.firstDaniel{
    display: flex;
    padding-top: 10vh;
    justify-content: space-around;
    background-color: rgb(21, 13, 64);
}

.firstDaniel .textcontainer{
    width: 30vw;
}

.firstDaniel .textcontainer h1{
    font-size: 3vw;
}

.firstDaniel .textcontainer p{
    font-size: 150%;
}

.firstDaniel .textcontainer a{
    color: white;
    font-size: 140%;
}

.secondDaniel{
    display: flex;
    justify-content: end;
    height: fit-content;
}

.secondDaniel .secondTextcontainer{
    width: 45vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 1rem;
    margin-right: 10rem;
}

.secondDaniel .secondTextcontainer p{
    font-size: 150%;
}

.secondDaniel .secondTextcontainer h1{
    font-size: 3vw;
}