.flexContainer {
    display: flex;
    height: fit-content;
    align-items: center;
    padding: 4rem;
    justify-content: start;
    padding-left: 4rem;
}

.text{
    display: flex;
    height: 100%;
    width: 30vw;
    justify-content: center;
    flex-direction: column;
}

.pic{
    height: 100%;
    align-items: center;
    display: flex;
    justify-content: center;
}

.text .button{
    background-color: rgb(110, 110, 110);
    padding: 0.5rem;
    padding-left: 3.5rem;
    padding-right: 3.5rem;
    border-radius: 10px;
    font-size: 150%;
    font-weight: bold;
    cursor: pointer;
}

.text h1{
    font-size: 3vw;
    margin: 0;
    padding: 0;
}

.text p{
    font-size: 150%;
}

